<template>
  <div class="createapply-formgroup-content">
    <div class="formgroup-copierList" v-if="copier.length!=0">
      <div class="formgroup-copierItem" v-for="item in copier">
        <img :src="item.url" @error="getImgurl">
        <h2>{{item.name}}</h2>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {}
  },
  mounted() {

  },
  created() {},
  computed: {},
  props: ['copier'],
  methods: {
    getImgurl(e) {
      e.currentTarget.src = '../../static/images/header_userimg.png';
      e.onerror = null;
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/mixin';
@import '../../style/apply';
.createapply-formgroup-content {
  margin-left: 0px;
}

</style>



