<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="content-title clear">
        <!-- <div class="left" v-if="type==1"></div>
        <div class="left" v-if="type==2"></div>
        <div class="left" v-if="type==3"></div> -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item v-for="(item,index) in routeList" :key="index" :to="{name:item.name,query:item.query}">{{item.name_str}}</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <div class="createapply-group">
        <div class="viewapply-topinfobox">
          <div class="viewapply-topinfo-title">
            {{fixation.title}}
            <img v-if="type==2&&mainStatus>1&&mainStatus!=5" class="viewapply-topinfo-resulticon" src="../../images/viewapply_successicon.png" alt="">
            <img v-if="type==1&&mainStatus==3&&mainStatus==4" class="viewapply-topinfo-resulticon" src="../../images/viewapply_successicon.png" alt="">
            <img v-if="mainStatus==-1" class="viewapply-topinfo-resulticon" src="../../images/viewapply_failicon.png" alt="">
            <img v-if="mainStatus==5" class="viewapply-topinfo-resulticon" src="../../images/viewapply_getbackicon.png" alt="">
          </div>
          <div class="viewapply-topinfo">
            <img class="viewapply-topinfo-comimg" :src="fixation.url" @error="getImgurl">
            <div class="viewapply-topinfo-textbox">
              <div class="viewapply-topinfo-text1">
                <h2>{{fixation.name}}</h2>
                <span>{{mainStatusStrList[type][mainStatus]}}</span>
              </div>
              <div class="viewapply-topinfo-text2">
                {{fixation.number}}
              </div>
            </div>
            <div class="viewapply-topinfo-btnbox">
              <span v-if="click.indexOf(1)!=-1" class="viewapply-topinfo-btn" @click="applyUrgentFun"></span>
              <span v-if="click.indexOf(4)!=-1" class="viewapply-topinfo-btn" @click="cancelApproveFun"></span>
              <span v-if="click.indexOf(2)!=-1" class="viewapply-topinfo-btn" @click="gotoEditapply(1)"></span>
              <span v-if="click.indexOf(3)!=-1" class="viewapply-topinfo-btn" @click="gotoEditapply(2)"></span>
              <span v-if="click.indexOf(6)!=-1" @click="derivePdfFun" class="viewapply-topinfo-btn"></span>
              <span v-if="click.indexOf(7)!=-1" @click="finishApproveFun" class="viewapply-topinfo-btn"></span>
              <span v-if="click.indexOf(8)!=-1" @click="initSignModal(1)" class="viewapply-topinfo-btn"></span>
              <span v-if="click.indexOf(9)!=-1" @click="initSignModal(2)" class="viewapply-topinfo-btn"></span>
              <span v-if="click.indexOf(5)!=-1" @click="approveFun" class="viewapply-topinfo-btn"></span>
              <!--             <span v-if="click==6" @click="derivePdfFun" class="viewapply-topinfo-btn"></span> -->
            </div>
          </div>
        </div>
        <div class="clear table-group-title">
          <div class="breadcrumb" style="margin-top:20px">
            <!-- <el-breadcrumb separator-class="el-icon-arrow-right" v-if="pageType==2">
            <el-breadcrumb-item :to="{ path: '/home/administrativeManageHomepage'}" v-if="pageType==3"></el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/home/investProjectManageHomepage' ,query: {id:this.$route.query.projectId}}" v-if="pageType==2"></el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/home/applyList',query:{projectId:this.$route.query.projectId}}" v-if="pageType==3"></el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/home/applyList'}" v-if="pageType==2"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>   
          </el-breadcrumb> -->
            <!-- <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item v-for="(item,index) in routeList" :key="index" :to="{name:item.name,query:item.query}">{{item.name_str}}</el-breadcrumb-item>
            </el-breadcrumb>
            <div class="line"></div> -->
          </div>
        </div>
        <div class="viewapply-content">
          <div class="viewapply-content-title viewapply-content-title-nobottom">
            {{fixation.sp_type}}
          </div>
          <div class="viewapply-content-normal">
            <div class="viewapply-content-item">
              <div class="viewapply-content-item-title"></div>
              <div class="viewapply-content-item-content">{{fixation.add_time}}</div>
            </div>
            <div class="viewapply-content-item" v-if="type==1">
              <div class="viewapply-content-item-title"></div>
              <div class="viewapply-content-item-content">{{fixation.depart}}</div>
            </div>
            <div class="viewapply-content-item" v-for="item in relax">
              <div class="viewapply-content-item-title">{{item.name}}</div>
              <div class="viewapply-content-item-content">{{item.value}}</div>
            </div>
          </div>
        </div>
        <div class="viewapply-content" v-if="type==2||type==3">
          <div class="viewapply-content-title viewapply-content-title-nobottom" v-if="type==2">
            
          </div>
          <div class="viewapply-content-title viewapply-content-title-nobottom" v-if="type==3">
            
          </div>
          <div class="viewapply-content-normal">
            <div class="viewapply-content-item">
              <div class="viewapply-content-item-title"></div>
              <div class="viewapply-content-item-content">
                <em v-if="file_list.length==0"></em>
                <div class="viewapply-content-item-imglist">
                  <div class="viewapply-content-item-imgItem" v-for="item in file_list">
                    <i class="filemanage-icon" :class="'icon-type-'+item.typestring"></i>
                    <div><a :href="item.url" target="_blank" download="">{{item.file_name}}</a></div>
                  </div>
                  <!-- <vue-images :imgs="imglist" :mousescroll="true" :modalclose="true"></vue-images> -->
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="viewapply-content">
          <div class="viewapply-content-title">
            
          </div>
          <apply-approvebox-show :approve="approve" :discuss="totaldiscuss" @createCommentFun="createCommentFun($event)" @createreplyCommentFun="createreplyCommentFun($event)" @cancelReplyFun="cancelReplyFun($event)" @changeShowallFun="changeShowallFun($event)" @createDiscuss="createDiscuss($event)" @changediscussShowall="changediscussShowall" type="approve" :aType="type"></apply-approvebox-show>
        </div>
        <div class="viewapply-content" v-if="segment.length!=0">
          <div class="viewapply-content-title" v-if="type==2">
            
          </div>
          <div class="viewapply-content-title" v-if="type==3">
            
          </div>
          <apply-approvebox-show :approve="segment" type="segment" :aType="type"></apply-approvebox-show>
          <!--         <apply-approvebox-show :splist="segment"></apply-approvebox-show> -->
        </div>
        <div class="viewapply-content">
          <div class="viewapply-content-title">
            <em class="viewapply-content-title-nodata-span" v-if="copier.length==0"></em>
          </div>
          <apply-copier :copier="copier"></apply-copier>
        </div>
      </div>
      <!--  -->
      <el-dialog title="" :visible.sync="createDialogShow" width="559px" center :append-to-body='true' class="applyfun-modal" :before-close="dialogClose">
        <div class="dialog_content">
          <div class="form-group-inputbox">
            <h2 class="form-group-inputbox-title"></h2>
            <div class="form-group-inputbox-selectbox select-normal-40 left">
              <textarea v-model="approveSubmitParam.content" placeholder="" class="textarea-normal" name=""></textarea>
            </div>
            <span class="form-group-inputbox-error"></span>
          </div>
          <div class="form-group-inputbox" v-if="type==2">
            <h2 class="form-group-inputbox-title"></h2>
            <div class="form-group-inputbox-fileselectbox">
              <input class="form-group-inputbox-fileselectbox-inputfile" type="file" multiple="multiple" @change="fileListChange($event)">
              <i class="el-icon-plus"></i>
            </div>
            <!-- <input class="form-group-inputbox-inputtext" type="text"> -->
            <span class="form-group-inputbox-error"></span>
            <div class="form-group-selectedfile-box">
              <div class="form-group-selectedfile-item clear" v-for="(item,index) in approveSubmitParam.fileList">
                <span class="filemanage-icon icon-wd"></span>
                <span class="form-group-selectedfile-filename">{{item.file_name}}</span>
                <span class="el-icon-close" @click="deleteFile(index)"></span>
              </div>
            </div>
          </div>
        </div>
        <div slot="footer" class="dialog_footer applyfun-modal-btnbox clear">
          <!-- <button v-if="key==1" v-for="(item,key) in button" class="dialog-btn-submit" @click="approveSubmit(key)">{{item}}</button> -->
          <button v-if="key==-1" class="dialog-btn-submit btn-blue-border" v-for="(item,key) in button" @click="approveSubmit(key)">{{item}}</button>
          <button v-if="key!=-1&&key!=0" class="dialog-btn-submit btn-blue" v-for="(item,key) in button" @click="approveSubmit(key)">{{item}}</button>
        </div>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="signDialogShow" width="500px" center :append-to-body='true' class="applyfun-modal">
        <div class="dialog_content">
          <div class="form-group-inputbox flex">
            <h2 class="form-group-inputbox-title"></h2>
            <div class="form-group-inputbox-signbox select-normal-40 left" ref="canvasHW">
              <canvas @mousedown="mouseDown" @mousemove="mouseMove" @mouseup="mouseUp" ref="canvasF"></canvas>
            </div>
          </div>
        </div>
        <div slot="footer" class="dialog_footer applyfun-modal-btnbox clear">
          <button v-for="(item,key) in button" class="dialog-btn-submit btn-blue" @click="signSubmit(key)">{{item}}</button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
//
import applyApproveboxShow from './applyApproverboxShow.vue' //
import applyCopier from './applyCopier.vue' //
import { approveShow, applyUrgent, cancelApprove, cancelLeave, derivePdf, finishApprove, approveResult, createComment, attachFile, createDicussapi, deleteFileUrl, signResult, leaveResult } from '../../config/api'
import vueImages from 'vue-images'
import { dialogConfirm, compareJson, dialogConfirmHasbtn } from '../../config/mUtils'
//import { getCreateApply, uploadApprove, submitApprove, getAccountModuleInfo, addAccount, deleteFile } from '../../config/api'
export default {
  data() {
    return {
      routeList: [],
      approve: '', //
      discuss: '', //
      totaldiscuss: {
        content: '',
        discusslist: '',
        show: false,
        showall: false,
      },
      segment: '', //
      fixation: '', //
      type: 1, //
      relax: '', //
      file_list: '', //
      approval_id: '',
      mainStatus: 1, //
      mainStatusStr: '', //
      is_mine: '', //1=> 2=>
      click: '',
      button: '', //
      copier: '',
      mainStatusStrList: {
        1: {
          '-1': '',
          0: '',
          1: '',
          2: '',
          3: '',
          4: '',
          5: ''
        },
        2: {
          '-1': '',
          0: '',
          1: '',
          2: '',
          3: '',
          4: '',
          5: ''
        },
        3: {
          0: '',
          1: '',
          3: '',
          4: '',
          5: ''
        }
      },
      ///////////////////////////
      createDialogShow: false,
      approveSubmitParam: {
        content: '',
        fileList: []
      },
      ////////////////
      signDialogShow: false,
      signDialogType:1,////1=>  2=>
      points: [],
      canvasTxt: null,
      startX: 0,
      startY: 0,
      moveY: 0,
      moveX: 0,
      endY: 0,
      endX: 0,
      w: null,
      h: null,
      isDown: false,
      page: 1,
    }
  },
  created() {
    this.routeList = this.$route.meta.routeList;
    this.routeList.forEach(element => {
      if(element.name_str==''){
        element.name_str = this.$route.query.aName;
      }
      // if(element.name_str==''){
      //   element.query.status = this.$route.query.selectStatus;
      //   element.query.current = this.$route.query.current;
      // }
      
    });
    //
    if (!this.$route.query.from) {
      this.pageType = '';
      this.$router.push({
        name: 'investProjectManageList'
      })
    } else {
      this.pageType = this.$route.query.from
    }
    //
    if (!this.$route.query.approval_id || !this.$route.query.status) {
      if (this.pageType == 2) {
        this.$router.push({
          name: 'investProjectManageList'
        })
      } else {
        this.$router.push({
          name: 'administrativeManageHomepageNew'
        })
      }
    } else {
      this.approval_id = this.$route.query.approval_id;
      this.is_mine = this.$route.query.status;
      this.initData();
    }
    if (this.$route.query.page) {
    		this.page = this.$route.query.page;
    }
  },
  components: {
    vueImages,
    applyApproveboxShow,
    applyCopier
  },
  computed: {},
  mounted() {},
  methods: {
    beforeRouteEnter(to, from, next) {
      next((vm) => {
        vm.realList = to.meta.routerList
      });
    },
    initData() {
      approveShow({
        approval_id: this.approval_id,
        type: '',
        is_mine: this.is_mine
      }).then(res => {
        if (res.data.msgNo == 1) {
          let approvelist = res.data.payload.approve;
          approvelist.forEach(item => {
            item.showOpinion = true;
            item.showComment = true;
            if (item.status == 0 || item.content == undefined || item.comment == undefined) {
              item.showOpinion = false;
            } else {
              if (item.content == '') {
                if (item.attach == undefined || item.attach.length == 0) {
                  item.showOpinion = false;
                } else {
                  item.showOpinion = true;
                }
              } else {
                item.showOpinion = true;
              }
              if (item.comment.length == 0) {
                item.showComment = false
              }
            };
            if (item.comment != undefined) {
              if (item.comment.length > 2) {
                item.showall = true;
              } else {
                item.showall = false;
              }
            }
            if (item.attach == undefined) {
              item.attach = [];
            } else {
              this.getFileType(item.attach)
            }
            item.newComment = {
              hid: item.hid,
              comment_id: 0,
              content: '',
              placeholder: ''
            };
          })
          this.approve = approvelist;
          this.segment = res.data.payload.segment;
          this.relax = res.data.payload.relax;
          this.type = res.data.payload.classify;
          this.click = res.data.payload.click;
          if (this.click.indexOf(10) != -1) {
            this.totaldiscuss.show = true;
          }
          if (res.data.payload.discuss != undefined) {
            this.totaldiscuss.discusslist = res.data.payload.discuss;
            if (this.totaldiscuss.discusslist.length > 2) {
              this.totaldiscuss.showall = true;
            } else {
              this.totaldiscuss.showall = false;
            }
          } else {
            this.totaldiscuss.discusslist = [];
          }
          this.file_list = res.data.payload.file_list;
          this.getFileType(this.file_list);
          this.fixation = res.data.payload.fixation;
          this.mainStatus = res.data.payload.mainStatus;
          this.button = res.data.payload.button;
          this.copier = res.data.payload.copier;
        }
      })
    },

    //
    createDiscuss(e) {
      createDicussapi({
        approval_id: this.approval_id,
        message: e
      }).then(res => {
        this.totaldiscuss.content = "";
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '');
          this.initData();
        } else {
          dialogConfirm('fail', res.data.message)
        }

      })
    },
    //
    createCommentFun(e) {
      this.approve[e.cindex].newComment.content = '';
      createComment(e.param).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '');
          this.initData();
        } else {
          dialogConfirm('error', res.data.message)
        }
      })
    },
    //
    createreplyCommentFun(e) {
      this.approve[e.cindex].newComment.content = '';
      this.approve[e.cindex].newComment.comment_id = e.comment_id;
      this.approve[e.cindex].newComment.placeholder = '' + e.replier;
    }, //
    cancelReplyFun(e) {
      this.approve[e.cindex].newComment.content = '';
      this.approve[e.cindex].newComment.comment_id = 0;
      this.approve[e.cindex].newComment.placeholder = '';
    },
    //
    changeShowallFun(e) {
      this.approve[e.cindex].showall = !this.approve[e.cindex].showall;
    },
    //
    changediscussShowall(e) {
      this.totaldiscuss.showall = !this.totaldiscuss.showall;
    },
    //
    applyUrgentFun() {
      applyUrgent({
        approval_id: this.approval_id
      }).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', res.data.payload)
        } else {
          dialogConfirm('fail', res.data.message)
        }
        this.initData();
      })
    },
    //
    cancelApproveFun() {
      if (this.type == 1) {
        cancelLeave({
          approval_id: this.approval_id
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '');
            this.signDialogShow = false;
          } else {
            dialogConfirm('fail', res.data.message)
          }
          this.initData();
        })
      } else {
        cancelApprove({
          approval_id: this.approval_id
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '');
            this.signDialogShow = false;
          } else {
            dialogConfirm('fail', res.data.message)
          }
          this.initData();
        })
      }

    },
    //pdf
    derivePdfFun() {
      derivePdf({
        approval_id: this.approval_id
      }).then(res => {
        if (res.data.msgNo == 1) {
          window.open(res.data.payload.url)
        } else {
          dialogConfirm('fail', res.data.message)
        }
      })
    },
    //
    finishApproveFun() {
      finishApprove({
        approval_id: this.approval_id
      }).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', res.data.payload)
        } else {
          dialogConfirm('info', res.data.message)
        }
        this.initData();
      })
    },
    //click
    approveFun() {
      this.createDialogShow = true;
    },
    //
    fileListChange(e) {
      let file = [];
      for (let i = 0; i < e.target.files.length; i++) {
        file.push(e.target.files[i]);
      }
      attachFile(file).then(res => {
        if (res.data.msgNo == 1) {
          res.data.payload.forEach(item => {
            this.approveSubmitParam.fileList.push(item);
          })
        } else {
          dialogConfirm('fail', res.data.message);
        }
        e.target.value = "";
      });

    },
    //
    deleteFile(index) {
      deleteFileUrl({
        path: encodeURIComponent(this.approveSubmitParam.fileList[index].url)
      }).then(res => {})
      this.approveSubmitParam.fileList.splice(index, 1);
    },
    //
    dialogClose() {
      this.deletefileFun();
      this.createDialogShow = false;
    },
    //
    deletefileFun() {
      this.approveSubmitParam.fileList.forEach(item => {
        deleteFileUrl({
          path: encodeURIComponent(item.url)
        }).then(res => {})
      })
      this.delFilelist = [];
    },
    //
    fileListChangeGetFileType(filename) {
      let type = filename.substring(filename.lastIndexOf("."), filename.length);
      let unsupportType = ['.3gpp', '.ac3', '.mp2', '.mp3', '.mp4', '.mpeg', '.mpg'];
      if (unsupportType.indexOf(type) == -1) {
        return true
      } else {
        return false
      }
    },
    //
    approveSubmit(type) {
      if (this.type == 1) {
        leaveResult({
          approval_id: this.approval_id,
          type: type,
          content: this.approveSubmitParam.content,
          //attachFile: JSON.stringify(this.approveSubmitParam.fileList)
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '')
            this.createDialogShow = false;
            this.initData();
          } else {
            dialogConfirm('fail', res.data.message);
          }

        })
      } else if (this.type == 2) {
        approveResult({
          approval_id: this.approval_id,
          type: type,
          content: this.approveSubmitParam.content,
          attachFile: JSON.stringify(this.approveSubmitParam.fileList)
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '')
            this.createDialogShow = false;
            this.initData();
          } else {
            dialogConfirm('fail', res.data.message);
          }
        })
      }

    },
    //
    getFileType(fileList) {
      let typelist = ['pdf', 'txt', 'ppt', 'xlsx', 'doc', 'docx', 'pic', 'zip', 'txt', 'rar', 'mp3'];
      let piclist = ['png', 'gif', 'bmp', 'tif', 'tiff', 'jpe', 'jpeg', 'jpg'];
      let pptlist = ['ppt', 'pptx'];
      let doclist = ['doc', 'docx'];
      let xlslist = ['xls', 'xlsx'];
      let ziplist = ['zip', 'rar'];
      let movlist = ['wmv', 'mp4'];
      let txtlist = ['txt', 'xml', 'html', 'css', 'js'];
      let mp3list = ['mp3', 'mp2', 'mpe', 'mpeg', 'mpg'];
      fileList.forEach(item => {
        let type = this.getFileTypeString(item.file_name);
        if (typelist.indexOf(type) != -1) {
          item.typestring = type;
        } else if (piclist.indexOf(type) != -1) {
          item.typestring = 'pic';
        } else if (pptlist.indexOf(type) != -1) {
          item.typestring = 'ppt';
        } else if (doclist.indexOf(type) != -1) {
          item.typestring = 'doc';
        } else if (xlslist.indexOf(type) != -1) {
          item.typestring = 'xls';
        } else if (txtlist.indexOf(type) != -1) {
          item.typestring = 'txt';
        } else if (mp3list.indexOf(type) != -1) {
          item.typestring = 'mp3';
        } else if (ziplist.indexOf(type) != -1) {
          item.typestring = 'rar';
        } else if (movlist.indexOf(type) != -1) {
          item.typestring = 'mov';
        } else {
          item.typestring = 'unno';
        }
      })
    },
    //
    getFileTypeString(filename) {
      filename = filename.toString();
      let type = filename.substring(filename.lastIndexOf(".") + 1, filename.length);
      return type;
    },
    //
    gotoEditapply(type) {
      this.$router.push({
        query: {
          approval_id: this.approval_id,
          type: type, //1=2=
          aType: this.type,
          from: this.pageType
        },
        name: 'createApply'
      })
    },
    getImgurl(e) {
      e.currentTarget.src = '../../static/images/header_userimg.png';
      e.onerror = null;
    },
    /////////////////////////////////////////
    initSignModal(type) {
      let that = this;
      that.signDialogShow = true;
      that.signDialogType = type;
      this.canvasTxt = null;
      let timer = setInterval(function() {
        if (that.$refs.canvasF != undefined) {
          let canvas = that.$refs.canvasF;
          canvas.height = that.$refs.canvasHW.offsetHeight;
          canvas.width = that.$refs.canvasHW.offsetWidth;
          that.canvasTxt = canvas.getContext("2d");
          that.canvasTxt.fillStyle = "white";
          that.canvasTxt.fillRect(0, 0, that.$refs.canvasHW.offsetHeight, that.$refs.canvasHW.offsetWidth);
          clearInterval(timer);
        }
      }, 500)
    },
    mouseDown(ev) {
      if (this.canvasTxt != null) {
        ev = ev || event;
        ev.preventDefault();
        if (1) {
          let obj = {
            x: ev.offsetX,
            y: ev.offsetY
          };
          this.startX = obj.x;
          this.startY = obj.y;
          this.canvasTxt.beginPath();
          this.canvasTxt.moveTo(this.startX, this.startY);
          this.canvasTxt.lineTo(obj.x, obj.y);
          this.canvasTxt.stroke();
          this.canvasTxt.closePath();
          this.points.push(obj);
          this.isDown = true;
        }
      }
    },
    mouseMove(ev) {
      ev = ev || event;
      ev.preventDefault();
      if (this.isDown) {
        let obj = {
          x: ev.offsetX,
          y: ev.offsetY
        };
        this.moveY = obj.y;
        this.moveX = obj.x;
        this.canvasTxt.beginPath();
        this.canvasTxt.moveTo(this.startX, this.startY);
        this.canvasTxt.lineTo(obj.x, obj.y);
        this.canvasTxt.stroke();
        this.canvasTxt.closePath();
        this.startY = obj.y;
        this.startX = obj.x;
        this.points.push(obj);
      }
    },
    mouseUp(ev) {
      ev = ev || event;
      ev.preventDefault();
      if (1) {
        let obj = {
          x: ev.offsetX,
          y: ev.offsetY
        };
        this.canvasTxt.beginPath();
        this.canvasTxt.moveTo(this.startX, this.startY);
        this.canvasTxt.lineTo(obj.x, obj.y);
        this.canvasTxt.stroke();
        this.canvasTxt.closePath();
        this.points.push(obj);
        this.points.push({ x: -1, y: -1 });
        this.isDown = false;
      }
    },
    signSubmit(key) {
      let canvas = this.$refs.canvasF;
      let imgBase = canvas.toDataURL("image/jpeg", 0.6);
      //let file = '';
      signResult({
        approval_id: this.approval_id,
        type: key,
        file: imgBase
      }).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '')
          this.signDialogShow = false;
          this.initData();
        } else {
          dialogConfirm('fail', res.data.message)
        }
      })
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal';
@import '../../style/apply';
.form-group-inputbox-fileselectbox {
  margin-bottom: 10px;
}

.applyfun-modal .textarea-normal {
  height: 100px;
}

.applyfun-modal .filemanage-icon {
  background: url('../../images/filemanage_icon.png') no-repeat;
  background-position: 0px -240px;
}

.applyfun-modal-btnbox {
  //margin-top: 15px;
  .dialog-btn-submit {
    margin-left: 20px;
  }
}

.form-group-inputbox-signbox {
  width: 302px;
  height: 302px;
  border: 1px solid #eee;
}

</style>



